body {
  
  margin: 0;
  padding: 0;
}

.fontTitle{
  font-family: 'Luckiest Guy', cursive !important
}

.fontBody{
  font-family: 'Fredoka One', cursive !important;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  
}

li {
  float: left;
}

li a {
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  margin-left: 20px;
}

/* li a:hover:not(.active) {
  background-color: #111;
} */

.active {
  border-bottom: solid 1px #d82c2e;
}

.btn-primary {
  color: #fff;
  border-color: #007bff !important;
  background-color: #3B9AEE !important;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 25px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  background: #3b9aee;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: #3b9aee;
  cursor: pointer;
}

.CrudForm{
  margin-top: 20px;
  width: 400px;
  padding: 7px 10px;
}

.ButtonCrud{
  margin-top: 20px;
  width: 200px;
}

.buttonAll{
  color: #fff;
